@font-face {
    font-family: 'icons';
    src: url('../fonts/icons.eot?s73he6');
    src: url('../fonts/icons.eot?s73he6#iefix') format('embedded-opentype'),
    url('../fonts/icons.ttf?s73he6') format('truetype'),
    url('../fonts/icons.woff?s73he6') format('woff'),
    url('../fonts/icons.svg?s73he6#icons') format('svg');
    font-weight: normal;
    font-style: normal;
    font-display: block;
}

[class^="_icon-"]::before,
[class*=" _icon-"]::before {
    /* use !important to prevent issues with browser extensions that change fonts */
    font-family: 'icons' !important;
    speak: never;
    font-style: normal;
    font-weight: normal;
    font-variant: normal;
    text-transform: none;
    line-height: 1;

    /* Better Font Rendering =========== */
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

._icon-facebook:before {
    content: "\e900";
}

._icon-instagram:before {
    content: "\e901";
}

._icon-link:before {
    content: "\e902";
}

._icon-telegram:before {
    content: "\e903";
}

._icon-brand:before {
    content: "\e903";
}

._icon-social:before {
